

img {
height: 100%;
object-fit: cover;
border-radius: 4px;   
}


@media (min-width: 768px) and (max-width: 1279px){
.news {
display: flex;
flex-wrap: wrap;
gap: 30px;

 }
}


@media (min-width: 1280px) {
.news {
display: flex;
flex-wrap: wrap;
gap: 32px; 
}

.news__item {
  flex-basis: calc((100% - 64px)/3);
}
}

@media (max-width: 767px) {
.news__item:not(:last-child) {
margin-bottom: 28px;
// height: 630px;
}

}
@media (min-width: 768px)and (max-width: 1279px) {
.news__item {
 flex-basis: calc((100% - 30px)/2);
     }
}

.news__img {
margin-bottom: 20px;
position: relative;
        
}
      
.news__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.4;
  letter-spacing: -0.01em;

  margin-bottom: 20px;

 }
 
 .news__text {
font-weight: 400;
font-size: 14px;
line-height: 1.3;

margin-bottom: 20px;

// white-space: nowrap; 

display: -webkit-box;
-webkit-box-orient: vertical;
-webkit-line-clamp: 3;
overflow: hidden; 
text-overflow: ellipsis; 

}

.news__link{
font-weight: 400;
font-size: 14px;
line-height: 1.3;

color: $accentColor;
}
.news__time {
font-weight: 400;
font-size: 14px;
line-height: 1.3;
color: $secondaryTextColor;

}

@media (min-width: 768px) {
  .news__title {
    font-size: 32px;
  }
  .news__text {
    font-size: 16px;
  }
  .button-card {
    right: 12px;
  }

  .news__link {
    font-size: 16px;
  }
  .news__time {
    font-size: 16px;
  }
}
     

.button-card-icon {
 fill: $accentColor;
 margin-left: 4px;

}
.news__category {
    
position: absolute;
top: 40px;
left: 0;

color: $whiteColor;
background-color: $accentBgColorOpasity;

font-weight: 500;
font-size: 12px;
line-height: 1.3;
text-align: center;


padding: 4px 8px;
border-radius: 0px 4px 4px 0px;
width: 112px;
height: 24px;

}
.button-card {
position: absolute;
bottom: 12px;
right: 8px;

padding: 6px 12px;
display: flex;
align-items: center;

  
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 1.3;
color: $darkTextColor;


background: $whiteColor;
border-radius: 20px;
border: none;

}

.news__info {
display: flex;
justify-content: space-between;

}
